.volumeControl {
    margin-left: 10px;
    border: 1px solid orange;
    border-radius: 3px;
    background-color: #4a1f1f;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    img {
        width: 20px;
        height: 20px;
        filter: invert(1); } }

@media (max-width: 900px) and (orientation: landscape) {
    .volumeControl {
        height: 30px;
        width: 30px;
        img {
            height: 20px;
            width: 20px; } } }
